<template>
  <div class="rank-wrapper">
    <div class="rank-list">
      <div class="rank-item" v-for="item in topList" :key="item.id" @click="rankDeatil(item.id)">
        <div class="left">
          <img :src="item.picUrl" alt="" />
          <div class="listenCount">
            <van-icon name="audio" />
            {{ (item.listenCount / 10000).toFixed(1) }}ä¸‡
          </div>
          
          <van-icon name="play-circle" class="bofang"/>
        </div>
        <div class="right">
          <div class="title">{{ item.topTitle }}</div>
          <ul>
            <li v-for="(song, index) in item.songList" :key="song.songname">
              <span style="margin-right: 8px">{{ index + 1 }}</span
              >{{ song.songname }}-{{ song.singername }}
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { onMounted, reactive, toRefs } from "vue";
import { useRouter } from 'vue-router';
export default {
  setup() {
    const state = reactive({
      topList: [],
    });
    const router = useRouter()
    const rankDeatil = (id)=>{
      router.push({path:'/rankDetail',query:{id:id}})
    }
    onMounted(() => {
      axios({
        url: "/api/getTopLists",
      }).then((res) => {
        state.topList = res.data.response.data.topList;
        console.log(res.data.response.data.topList);
      });
    });
    return {
      ...toRefs(state),
      rankDeatil
    };
  },
};
</script>

<style lang="less" scoped>
.rank-wrapper {
  width: 1040px;
  margin-top: 20px;
  .rank-list {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    .rank-item {
      cursor: pointer;
      margin: 10px;
      width: 44%;
      display: flex;
      background-color: #f2f2f2;
      border-radius: 10px;
      .left {
        position: relative;
        img {
          width: 200px;
          border-radius: 10px;
        }
        .listenCount {
          position: absolute;
          position: absolute;
          right: 17px;
          bottom: 21px;
          background: #111;
          color: #fff;
          width: 81px;
          height: 26px;
          line-height: 26px;
          text-align: center;
          font-size: 14px;
          border-radius: 10px;
        }
        .bofang{
          position: absolute;
          font-size: 90px;
          position: absolute;
          left: 57px;
          top: 50px;
          display: none;
          color: #ffffff;
        }
        .bofang:hover{
          color: #1ECFA3;
        }
      }
      .right {
        padding: 20px;
        .title {
          font-size: 20px;
          font-weight: bold;
        }
        ul {
          margin-top: 14px;
          li {
            width: 187px;
            height: 30px;
            line-height: 30px;
            font-size: 13px;
            color: #8a8a8a;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
          }
        }
      }
    }
    .rank-item:hover {
      transform: translateY(-12px);
      transition: 0.4s;
      .bofang{
        display: block;
      }
      .listenCount{
        display: none;
      }
    }
  }
}
</style>